import Vue from 'vue'
import VueRouter from 'vue-router'
import picturefloat from '../views/pages/picturefloat'
import Index from '../views/index'
import SearchBox from '../views/pages/searchbox'
import LoadingCss from '../views/pages/loading_css'
import BubbleFloating from '../views/pages/bubble_floating'
import NeonButton from '../views/pages/neon_button'

Vue.use(VueRouter)

const routes = [
  {
    path: '/picturefloat',
    name: '图片悬浮效果',
    component: picturefloat
  },
  {
    path: '/searchbox',
    name: '动态搜索框效果',
    component: SearchBox
  },
  {
    path: '/loading_css',
    name: '炫酷加载效果',
    component: LoadingCss
  },
  {
    path: '/bubble_floating',
    name: '气泡浮动效果',
    component: BubbleFloating
  },
  {
    path: '/neon_button',
    name: '霓虹灯按钮效果',
    component: NeonButton
  },
  {
    path: '/index',
    name: 'index',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    // component: () => import(/* webpackChunkName: "about" */ '../views/index.vue')
    component: Index
  },
  {
    path: '*',
    component: Index
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
